<template>
  <div class="float-left">
    <section class="information">
      <article class="information_text">
        <h4 id="page_title"></h4>
      </article>

      <b-table
        sticky-header="660px"
        class="mb-3"
        striped
        hover
        :fields="fields"
        :items="apts"
        fixed
      >
        <template #cell(aptCode)="row">
          <input sm="10" size="lg" type="checkbox" aria-label="Checkbox for
          following text input" @change="checkBoxClicked(row.item, $event)"
          class="checkBox ml-4 mr-n2" id = />
        </template>
        <template #cell(apartmentName)="row">
          <a id="mouseOver" size="sm" @click="getDeal(row.item)" class="mr-2">
            {{ row.item.apartmentName }}
          </a>
          <!-- <a
            v-b-toggle.my-collapse
            class="mouseOver"
            @click="getDetail(data.item.aptCode)"
          >
            {{ data.item.aptName }}
          </a> -->
        </template>
        <template #cell(jibun)="row">
          <div colspan="2">
            {{ row.item.dong + " " + row.item.jibun }}
          </div>
        </template>
        <!--  ->      -->
        <template #row-details="row">
          <b-card v-for="(deal, index) in deals" :key="index">
            <b-row class="mb-2">
              <b-col sm="3" class="text-sm-right"><b>:</b></b-col>
              <b-col>{{ row.item.apartmentName }}</b-col>
            </b-row>

            <b-row class="mb-2">
              <b-col sm="3" class="text-sm-right"><b> :</b></b-col>
              <b-col>{{ deal.dealAmount }}</b-col>
            </b-row>
            <b-row class="mb-2">
              <b-col sm="3" class="text-sm-right"><b> :</b></b-col>
              <b-col>{{
                deal.dealYear + "." + deal.dealMonth + "." + deal.dealDay
              }}</b-col>
            </b-row>
            <b-row class="mb-2">
              <b-col sm="3" class="text-sm-right"><b>/:</b></b-col>
              <b-col>{{ deal.floor + "/" + deal.dealAmount }}</b-col>
            </b-row>
          </b-card>
          <b-button
            class="container text-center"
            id="closeBtn"
            pill
            variant="info"
            @click="row.toggleDetails"
            ></b-button
          >
        </template>
      </b-table>
    </section>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      fields: [
        {
          key: "aptCode",
          label: "",
        },
        {
          key: "apartmentName",
          label: "",
        },
        {
          key: "jibun",
          label: "",
          formatter: "fullAddress",
        },
        {
          // A regular column with custom formatter
          key: "buildYear",
          label: "",
        },
      ],
    };
  },
  created() {
    this.selected.splice(0, 4);
  },
  methods: {
    ...mapActions("aptStore", ["aptClear", "getDealInfo", "dijkstraClear"]),
    getDeal(item) {
      // collapse    -> getDealInfo     
      if (item._showDetails) {
        this.$set(item, "_showDetails", false);
      } else {
        this.$nextTick(() => {
          this.$set(item, "_showDetails", true);
        });
      }
      console.log("apt item: ", item.lat);
      this.getDealInfo(item);
    },
    fullAddress(value) {
      // console.log(value);
      // console.log(this.address);
      return this.address + value;
    },
    checkBoxClicked(value, event) {
      this.dijkstraClear();
      //   4    true  ->   
      if (this.selected.length >= 4 && event.target.checked) {
        alert("   4    ");
        // console.log(this.selected);
        // console.log(event);
        event.target.checked = false;
        return;
      }
      for (let i = 0; i < this.selected.length; i++) {
        if (this.selected[i] == value) {
          //    ->     
          this.selected.splice(i, 1);
          return; //  
        }
      }
      console.log(value);
      if (event.target.checked) {
        //    
        this.selected.push(value);
      }
    },
  },
  computed: {
    ...mapState("aptStore", ["apts", "deals", "selected"]),
  },
  watch: {
    //          
    selected() {
      if (this.selected.length == 0) {
        const checkBox = document.getElementsByClassName("checkBox");
        // console.log(checkBox[0].checked);
        const cbSize = checkBox.length;
        for (let i = 0; i < cbSize; i++) {
          checkBox[i].checked = false;
        }
      }
    },
  },
};
</script>

<style scoped>
#mouseOver {
  cursor: pointer;
}
table {
  height: "400px";
}
#closeBtn {
  position: relative;
  text-align: center;
}
.checkBox {
  text-align: center;
}
input[type="checkbox"] {
  transform: scale(1.8);
}
</style>
